.ContactForm {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 300px;
    margin: auto;
    border: 2px solid #AFEEEE;
  }

.label {
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 15px;
    font-size: 18px;
    font-style: italic;
    font-weight: 500;
    color: #000;
  }

.input {
    margin-bottom: 25px;
    margin-left: 15px;
    padding: 5px;
  }

.button {
    margin: 0 0 15px 15px;
    padding: 10px;
    font-size: 18px;
    cursor: pointer;
    outline: none;
    border: inherit;
    border-radius: 5px;
    background-color: #AFEEEE;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    transition: box-shadow 250ms linear;
  }
  
.button:hover {
  box-shadow: 2px 5px 2px 0 rgb(0 0 0 / 0.5);
  }
